.mobile-menu {
  width: 100vw;
  height: 100vh;
  z-index: 2;
  background-color: var(--mobile-menu-bg-color);
  opacity: 1;
  transition: opacity var(--duration) var(--timing-function), transform var(--duration) var(--timing-function);
  position: fixed;
  top: 0;
  right: 0;
  overflow-y: scroll;
  transform: translateX(0);
}

