p {
    margin: 0
}
.about {
    
    padding-bottom: 40px;
    background: var(--howitsmade-bg-color);
    @media screen and (min-width: $tablet) {
        padding-bottom: 80px;
    }
        @media screen and (max-width: $tablet) {
            padding-top: 40px;}
        @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding-top: 20px;  }
         @media screen and (min-width: $desktop) {
             padding-top: 60px;
            padding-bottom: 60px;
         }
}
.about-title{
    @media screen and (max-width:$tablet ){
    margin-bottom: 32px}

        
    
}
.about-text {
    
    @extend %secondary-font;
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 1.16;
        text-align: center;
        text-transform: uppercase;
        
        margin-bottom: 15px;

    color: var(--light-pink-color);

        @media screen and (min-width: $tablet) {
          
            margin-bottom: 16px;
            font-size: 26px;
             line-height: 1.15;
        }
                 @media screen and (min-width: $desktop) {
                  
                     margin-bottom: 16px;
                    font-size: 30px;
                    line-height: 1.13;
                 }

}

.about{
    @media screen and (max-width:767px){
    background-image: url(..//images/mobile/mob-bcg-milk-1x.png);
    background-repeat: no-repeat;
    background-attachment:scroll;
  
    background-position: bottom 27% left;
    @media screen and (min-width: 480px) and (max-width: 767px){
        background-position: bottom 28% center;
    }
}
  @media screen and (max-width: 480px){  background-size: 100%;}
}

    // @media screen and (max-width:767px) {
    //     @media screen and (min-device-pixel-ratio: 2),
    //         (min-resolution:192dpi),
    //         (min-resolution:2dppx) {
    //             .about {
    //                 background-image:
    //                     url(..//images/mobile/mob-bcg-milk-2x.png);
    //             }
        
    //         }
    // }
.about{
        @media screen and (min-width:1200px) {
                background-image: url(..//images/desktop/desk-bcg-milk-1x.png);
                background-repeat: no-repeat;
                background-attachment: scroll;
                background-position: center bottom 14%;
           
        }
    }






.about-name {
         @extend %secondary-font;
        font-style: normal;
        font-weight: 400;
        font-size: 34px;
        line-height: 1.14;
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: var(--dark-pink-color);
        margin: 0;
        

    @media screen and (min-width: $tablet) {
            font-size: 48px;
                line-height: 1.14;
                margin-bottom: 40px;
        }
    
        @media screen and (min-width: $desktop) {
            margin-bottom: 60px;
        font-size: 58px;
            line-height: 1.13;
           
        }
}

.about-img{
    width: 100%;
}
.about-list__item {
   
        margin-bottom: 40px;
        @media screen and (max-width: $tablet) {
            margin-top: 40px;
        }
}

.about-list__item--another{
   margin-bottom: 20px;
@media screen and (min-width: $tablet) {
    margin-bottom: 40px;
    }

}
.about-picture {
    @media screen and (min-width: 480px) and (max-width: 767px){
    display: flex;
    justify-content: center;}
        @media screen and (min-width: $tablet) {
            margin-right: 32px;
        }
                 @media screen and (min-width: $desktop) {
                    margin-right: 139px
                 }
    
}

.about-pozition{
    @media screen and (min-width: 480px) and (max-width: 767px) {
            display: flex;
                flex-direction: column;
                align-items: center;
            }
                         @media screen and (min-width: 768px)  
                         {
                             display: flex;
 }
    }



        
.about-picture__another{
    width: 100%;
    
}
.about-list__text {
    width: 74%;
       
    margin-bottom: 40px;
    font-weight: 500;
        font-size: 12px;
        line-height: 1.58;
    color: var(--primary-black-color);
    @media screen and (min-width: 480px) and (max-width:767px) {
        width: 210px;
    
    }
    @media screen and (min-width: $tablet) {
        width: 334px;
        font-size: 14px;
            line-height: 1.78;
            
        }
        @media screen and (min-width: $desktop) {
            width: 425px;
            margin-top: 72px;
                margin-bottom: 32px;
             font-size: 16px;
            line-height: 1.75;
            
        }
         @media screen and (max-width: $tablet){
             margin-top: 40px;
         

         }
        
}
.another{
    margin-top: 10px;
    @media screen and (min-width: $desktop) {
        margin-top: 22px;}
}
.about-list__desc {
    
    width: 73%;
          
        font-weight: 500;
        font-size: 12px;
        line-height: 1.58;
    color: var(--text-gray-color);

    
@media screen and (min-width: 480px) and (max-width:767px) {
    width: 197px;

}
    @media screen and (min-width: $tablet) {
        width: 320px;
        letter-spacing: 0.04em;
        font-size: 14px;
            line-height: 1.78;
            
        }
    
        @media screen and (min-width: $desktop) {
            width: 440px;
            letter-spacing: 0.04em;
        font-size: 14px;
            line-height: 1.92;
        }
                
}
.about-btn {
    margin-top: 20px;
        font-weight: 700;
        font-size: 14px;
        line-height: 18px;
        
        color: #D41443;
        width: 176px;
        height: 44px;
        background: var(--primary-white-color);
        border-radius: 22px;
        cursor: pointer;
        @media screen and (min-width: $tablet) {
            margin-top: 40px;
        }
              
}
.about-svg {
    margin-left: 13px;
    --color1: var(--dark-pink-color);
    
}

