.footer {
    @extend %secondary-font;
    color: var(--dark-pink-color);
}

.footer {
    background-color: var(--howitsmade-bg-color);
    width: 100%;
    padding-bottom: 20px;
    padding-top: 24px;
    
}

.footer__title {
    display: flex;
    flex-direction: column;
    margin-top: 0px;
    margin-bottom: 16px;
    max-width: 232px;

    font-size: 18px;
    line-height: 1.16;
    text-transform: uppercase;
    color: var(--light-pink-color);
}

.footer__subtitle {
    padding-top: 16px;
    margin-top: 0px;
    margin-bottom: 40px;
    max-width: 200px;

    color: var(--dark-pink-color);
    font-size: 34px;
    line-height: 1.14;
    letter-spacing: 0.04em;
    text-transform: uppercase;
}

.footer__social--item {
    width: 32px;
    height: 32px;
    border-radius: 50%;

    &:focus,
    &:hover {
        background-color: var(--dark-pink-color);
    }
}

.footer__social--item:not(:last-child) {
    margin-right: 28px;
}

.footer__social--list {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 40px;
}

.footer__social--link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 32px;
    height: 32px;
    background-color: var(--icon-bg-color);
    cursor: pointer;
    border-radius: 50%;
    
    transition-property: background-color;
    transition-duration: var(--duration);
    transition-timing-function: var(--timing-function);

    &:hover,
    &:focus {
        background-color: var(--dark-pink-color);
    }
}

.footer__tel {
    
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.86;
    color: var(--primary-black-color);

    &:hover,
    &:focus {
        color: var(--dark-pink-color);
    }
}

.footer__tel--number {
    color: var(--dark-pink-color);
}

.footer__tel--link {
    text-align: right;
    margin-bottom: 40px;
}

.footer__container {
   border-bottom: 1px solid var(--review-element-color);
   margin-bottom: 16px;
}

.footer__copyright {   
    margin-top: 0px;
    margin-bottom: 0px;

    font-family: 'DM Sans';
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    text-align: center;
    color: var(--text-gray-color);
    max-width: 150px;

    margin-left: auto;
    margin-right: auto;
}

.section-footer {
    @media screen and (min-width: $tablet) {
        width: $tablet;
        padding-right: 16px;
        padding-left: 16px;
        display: flex;
        justify-content: space-between;
       }
}



